<template>
    <nav class="navbar orange lighten-1 ">
        <div class="nav-wrapper col s12">
            <div class="navbar-left">
                <a href="#" @click.prevent="$emit('onSidebar')">
                    <i class="material-icons black-text">dehaze</i>
                </a>
                <span class="black-text hide-on-small-and-down">{{date | date('date')}}</span>
            </div>
            <ul class="right">
                <li>
                    <a
                        class="dropdown-trigger black-text"
                        href="#"
                        data-target="dropdown"
                        ref="dropdown"
                    >
                    {{name}}
                    <i class="material-icons right">arrow_drop_down</i>
                    </a>

                    <ul id='dropdown' class='dropdown-content'>
                    <li>
                        <router-link to="/profile" class="black-text">
                        <i class="material-icons">account_circle</i>Профиль
                        </router-link>
                    </li>
                    <li class="divider" tabindex="-1"></li>
                    <li>
                        <a href="#" class="black-text" @click.prevent="logout">
                        <i class="material-icons">assignment_return</i>Выйти
                        </a>
                    </li>
                    </ul>
                </li>
            </ul>
        </div>
    </nav>
</template>
<script>
export default {
    data: ()=>({
        dropdown: null,
        date: new Date(),
        interval: null
    }),
    mounted(){
        this.dropdown = M.Dropdown.init(this.$refs.dropdown, {constrainWidth: false, alignment: 'right', inDuration: 500})
    },
    beforeDestroy(){
        if(this.dropdown && this.dropdown.destroy){ this.dropdown.destroy()}
    },
    methods: {
        async logout(){
            await this.$store.dispatch('logout')
            this.$router.push('/login?message=logout')
        }
    },
    computed: {
        name(){
           return this.$store.getters.info.name
        }
    }

}
</script>